          <fhir-reference field="family_head" :initial="family_head" :slotProps="{ source: { path: 'family_head', data: [] }}" :edit="true" path="family_head" id="Basic.extension:head.value[x]:valueReference" definition="http://ihris.org/fhir/StructureDefinition/itsf-family-head-profile#Basic.extension:head.value[x]:valueReference" report="ihris-es-itsf-family-head" reportReturnValue="ihris-es-itsf-family-head.id" referenceDisplayPath="name.where(use='official').given.first() + ' ' + name.where(use='official').family" label="Family Head" displayType="reportSelect" min="0" max="1"></fhir-reference>
