          <fhir-attachment field="pay_receipt" :slotProps="{ source: { path: 'pay_receipt', data: [] }}" :edit="true" path="pay_receipt" displayCondition="amount|!=|0" id="Basic.extension:receipt.value[x]:valueAttachment" definition="http://ihris.org/fhir/StructureDefinition/itsf-school-fees-payment#Basic.extension:receipt.value[x]:valueAttachment" maxValueAttachment="20MB" attachment-types="image/*,.pdf" label="Payment Receipt" min="1" max="1"></fhir-attachment>
          <fhir-attachment field="exemption_letter" :slotProps="{ source: { path: 'exemption_letter', data: [] }}" :edit="true" path="exemption_letter" displayCondition="school_fees_type|=|Basic/exempted" id="Basic.extension:exemption-letter.value[x]:valueAttachment" definition="http://ihris.org/fhir/StructureDefinition/itsf-school-fees-payment#Basic.extension:exemption-letter.value[x]:valueAttachment" maxValueAttachment="20MB" attachment-types="image/*,.pdf" label="Exemption Letter" min="1" max="1"></fhir-attachment>
          <fhir-date field="pay_date" :slotProps="{ source: { path: 'pay_date', data: [] }}" :edit="true" path="pay_date" displayCondition="" id="Basic.extension:date.value[x]:valueDate" definition="http://ihris.org/fhir/StructureDefinition/itsf-school-fees-payment#Basic.extension:date.value[x]:valueDate" minValueDate="1920-01-01" maxValueDate="2030-01-01" label="Date Paid" min="1" max="1"></fhir-date>
