<script>
export default {
  props: ["queries"],
  data() {
    return {
      fullname: "",
      regStatus: "",
      regStatusCode: "",
      edit: false,
      overlay: false,
      registration: {},
      valid: true,
      amount: 0,
      pay_receipt: "",
      exemption_letter: "",
      pay_date: "",
      unit: "",
      stream: "",
      accomodation: "",
      student_type: "",
      school_fees_type: "",
      family_head_category: "",
      family_head: "",
      members: [],
      membersHeaders: [{
        text: this.$t("App.hardcoded-texts.Name"),
        value: "name"
      }, {
        text: this.$t("App.hardcoded-texts.Registration Number"),
        value: "regNum"
      }, {
        text: this.$t("App.hardcoded-texts.Fees Category"),
        value: "feecat"
      }, {
        text: this.$t("App.hardcoded-texts.Required Fee"),
        value: "requiredfee"
      }, {
        text: this.$t("App.hardcoded-texts.Paid Fee"),
        value: "paidfee"
      }, {
        text: this.$t("App.hardcoded-texts.Pending Fee"),
        value: "pendingfee"
      }],
      searchMember: "",
      familyMembersPaidFee: true,
      controlNumber: "",
      payments: {},
    }
  },
  computed: {
    isMobile() {
      if(screen.width > 760) {
        return false
      }
      return true
    }
  },
  components: {
    "fhir-integer": () => import(/* webpackChunkName: "fhir-main" */ "@/components/fhir/fhir-integer" ),
    "fhir-choice": () => import(/* webpackChunkName: "fhir-main" */ "@/components/fhir/fhir-choice" ),
    "fhir-reference": () => import(/* webpackChunkName: "fhir-main" */ "@/components/fhir/fhir-reference" ),
    "fhir-attachment": () => import(/* webpackChunkName: "fhir-main" */ "@/components/fhir/fhir-attachment" ),
    "fhir-date": () => import(/* webpackChunkName: "fhir-main" */ "@/components/fhir/fhir-date" ),
  },
  methods: {
    getActiveFamilyMembers() {
      fetch("/site/student/getFamilyMembers/" + this.queries.practitioner).then((response) => {
        if(response.ok) {
          response.json().then((response) => {
            this.members = response
            if(response.length) {
              for(let member of this.members) {
                fetch("/site/student/latestRegistration/" + member.id).then((response) => {
                  if(response.ok) {
                    response.json().then((response) => {
                      if(member.requiredfee && !member.paidfee) {
                        this.familyMembersPaidFee = false
                      }
                      member.reg_resource = response.reg_resource
                      member.reg_resource.extension.push({
                        url: "http://ihris.org/fhir/StructureDefinition/registration-status",
                        valueCoding: {
                          system: "http://ihris.org/fhir/CodeSystem/registration-status",
                          code: "pending",
                          display: "Pending"
                        }
                      })
                      member.family_head_resource = {
                        resourceType: "Basic",
                        meta: {
                          profile: [ "http://ihris.org/fhir/StructureDefinition/itsf-family-head-profile" ]
                        },
                        extension: [{
                          url: "http://ihris.org/fhir/StructureDefinition/family-head",
                          valueReference: {
                            reference: "Practitioner/" + this.queries.practitioner
                          }
                        }]
                      }
                      member.fees = {
                        resourceType: "Basic",
                        meta: {
                          profile: [ "http://ihris.org/fhir/StructureDefinition/itsf-school-fees-payment" ]
                        },
                        extension: [ {
                          url: "http://ihris.org/fhir/StructureDefinition/itsf-payments-amount",
                          valueInteger: 0
                        }, {
                          url: "http://ihris.org/fhir/StructureDefinition/ihris-practitioner-reference",
                          valueReference: {
                            reference: "Practitioner/" + member.id
                          }
                        }]
                      }
                    })
                  }
                })
              }
            }
          })
        } else {
          this.$store.commit("setMessage", {
            type: "error",
            text: "An error has occured, try again later",
          });
        }
      })
    },
    getLatestReg() {
      fetch("/site/student/latestRegistration/" + this.queries.practitioner).then((response) => {
        if(!response.ok) {
          this.overlay = false
          response.json().then((response) => {
            if(response.payments) {
              this.payments = response.payments
            }
            if(response.controlNumber && response.controlNumber.data.length) {
              this.controlNumber = response.controlNumber.data[0].hijiridate + response.controlNumber.data[0].id
            }
            this.$store.commit("setMessage", {
              type: "error",
              text: response.message,
            });
          })
        } else
        response.json().then((response) => {
          this.overlay = false
          this.unit = response.reg_resource.extension.find((ext) => {
            return ext.url === "http://ihris.org/fhir/StructureDefinition/itsf-maahad-unit"
          })?.valueCoding
          this.stream = response.reg_resource.extension.find((ext) => {
            return ext.url === "http://ihris.org/fhir/StructureDefinition/itsf-maahad-stream"
          })?.valueCoding
          this.accomodation = response.reg_resource.extension.find((ext) => {
            return ext.url === "http://ihris.org/fhir/StructureDefinition/itsf-acommodation"
          })?.valueCoding
          this.student_type = response.reg_resource.extension.find((ext) => {
            return ext.url === "http://ihris.org/fhir/StructureDefinition/itsf-student-type"
          })?.valueCoding
          this.school_fees_type = response.reg_resource.extension.find((ext) => {
            return ext.url === "http://ihris.org/fhir/StructureDefinition/itsf-school-fees-type"
          })?.valueReference
          this.family_head_category = response.reg_resource.extension.find((ext) => {
            return ext.url === "http://ihris.org/fhir/StructureDefinition/family-head-category"
          })?.valueCoding
          if(response.family_head_resource) {
            this.family_head = response.family_head_resource.extension.find((ext) => {
              return ext.url === "http://ihris.org/fhir/StructureDefinition/family-head"
            })?.valueReference
          }
          this.registration = response
        })
      }).catch(() => {
        this.overlay = false
        this.$store.commit("setMessage", {
          type: "error",
          text: "An error has occured, try later",
        });
      })
    },
    save() {
      this.overlay = true;
      this.populateValues(this.$children)
      let schoolFees = {
        resourceType: "Basic",
        meta: {
          profile: [ "http://ihris.org/fhir/StructureDefinition/itsf-school-fees-payment" ]
        },
        extension: [ {
          url: "http://ihris.org/fhir/StructureDefinition/itsf-payments-amount",
          valueInteger: this.amount
        }, {
          url: "http://ihris.org/fhir/StructureDefinition/itsf-payments-date",
          valueDate: this.pay_date
        }, {
          url: "http://ihris.org/fhir/StructureDefinition/itsf-exemption-letter",
          valueAttachment: this.exemption_letter
        }, {
          url: "http://ihris.org/fhir/StructureDefinition/itsf-payments-receipt",
          valueAttachment: this.pay_receipt
        }, {
          url: "http://ihris.org/fhir/StructureDefinition/ihris-practitioner-reference",
          valueReference: {
            reference: "Practitioner/" + this.queries.practitioner
          }
        }]
      }
      let index = this.registration.reg_resource.extension.findIndex((ext) => {
        return ext.url === "http://ihris.org/fhir/StructureDefinition/itsf-maahad-unit"
      })
      this.registration.reg_resource.extension[index].valueCoding = this.unit
      index = this.registration.reg_resource.extension.findIndex((ext) => {
        return ext.url === "http://ihris.org/fhir/StructureDefinition/itsf-maahad-stream"
      })
      this.registration.reg_resource.extension[index].valueCoding = this.stream
      index = this.registration.reg_resource.extension.findIndex((ext) => {
        return ext.url === "http://ihris.org/fhir/StructureDefinition/itsf-acommodation"
      })
      this.registration.reg_resource.extension[index].valueCoding = this.accomodation
      index = this.registration.reg_resource.extension.findIndex((ext) => {
        return ext.url === "http://ihris.org/fhir/StructureDefinition/itsf-student-type"
      })
      this.registration.reg_resource.extension[index].valueCoding = this.student_type
      index = this.registration.reg_resource.extension.findIndex((ext) => {
        return ext.url === "http://ihris.org/fhir/StructureDefinition/itsf-school-fees-type"
      })
      this.registration.reg_resource.extension[index].valueReference.reference = this.school_fees_type.reference
      index = this.registration.reg_resource.extension.findIndex((ext) => {
        return ext.url === "http://ihris.org/fhir/StructureDefinition/family-head-category"
      })
      index = this.registration.reg_resource.extension.findIndex((ext) => {
        return ext.url === "http://ihris.org/fhir/StructureDefinition/registration-status"
      })
      if(index == -1) {
        this.registration.reg_resource.extension.push({
          url: "http://ihris.org/fhir/StructureDefinition/registration-status",
          valueCoding: {
            system: "http://ihris.org/fhir/CodeSystem/registration-status",
            code: "pending",
            display: "Pending"
          }
        })
      }
      index = this.registration.reg_resource.extension.findIndex((ext) => {
        return ext.url === "http://ihris.org/fhir/StructureDefinition/family-head-category"
      })
      if(this.school_fees_type.reference == "Basic/familyfive" || this.school_fees_type.reference == "Basic/familynine" || this.school_fees_type.reference == "Basic/familyinfinity") {
        if(index === -1) {
          this.registration.reg_resource.extension.push({
            url: "http://ihris.org/fhir/StructureDefinition/family-head-category",
            valueCoding: this.family_head_category
          })
        } else {
          this.registration.reg_resource.extension[index].valueCoding = this.family_head_category
        }
      } else if(index > -1) {
        this.registration.reg_resource.extension.splice(index, 1)
      }
      let rolebundle = {
        resource: this.registration.reg_resource,
        request: {
          method: "POST",
          url: "PractitionerRole"
        }
      }
      if(this.regStatusCode === 'new') {
        rolebundle.request.method = "PUT"
        rolebundle.request.url = "PractitionerRole/" + this.registration.reg_resource.id
      }
      let bundle = {
        entry: [rolebundle]
      }
      if(this.regStatusCode === 'new') {
        let financestatus = {
          resourceType: "Basic",
          meta: {
            profile: ["http://ihris.org/fhir/StructureDefinition/finance-registration-review-profile"]
          },
          extension: [{
            url: "http://ihris.org/fhir/StructureDefinition/ihris-practitioner-reference",
            valueReference: {
              reference: "Practitioner/" + this.queries.practitioner
            }
          }, {
            url: "http://ihris.org/fhir/StructureDefinition/registration-reference",
            valueReference: {
              reference: "PractitionerRole/" + this.registration.reg_resource.id
            }
          }, {
            url: "http://ihris.org/fhir/StructureDefinition/registration-status",
            valueCoding: {
              system: "http://ihris.org/fhir/CodeSystem/registration-status",
              code: "pending",
              display: "Pending"
            }
          }, {
            url: "http://ihris.org/fhir/StructureDefinition/start-date",
            valueDate: this.$moment().format("YYYY-MM-DD")
          }]
        }
        let taalimstatus = {
          resourceType: "Basic",
          meta: {
            profile: ["http://ihris.org/fhir/StructureDefinition/taalim-registration-review-profile"]
          },
          extension: [{
            url: "http://ihris.org/fhir/StructureDefinition/ihris-practitioner-reference",
            valueReference: {
              reference: "Practitioner/" + this.queries.practitioner
            }
          }, {
            url: "http://ihris.org/fhir/StructureDefinition/registration-reference",
            valueReference: {
              reference: "PractitionerRole/" + this.registration.reg_resource.id
            }
          }, {
            url: "http://ihris.org/fhir/StructureDefinition/registration-status",
            valueCoding: {
              system: "http://ihris.org/fhir/CodeSystem/registration-status",
              code: "pending",
              display: "Pending"
            }
          }]
        }
        bundle.entry.push({
          resource: financestatus,
          request: {
            method: "POST",
            url: "Basic"
          }
        })
        bundle.entry.push({
          resource: taalimstatus,
          request: {
            method: "POST",
            url: "Basic"
          }
        })
      }
      if(this.family_head.reference) {
        let family_head_resource = {
          resourceType: "Basic",
          meta: {
            profile: [ "http://ihris.org/fhir/StructureDefinition/itsf-family-head-profile" ]
          },
          extension: [{
            url: "http://ihris.org/fhir/StructureDefinition/family-head",
            valueReference: {
              reference: this.family_head.reference
            }
          }]
        }
        bundle.entry.push({
          resource: family_head_resource,
          request: {
            method: "POST",
            url: "Basic"
          }
        })
      }
      bundle.entry.push({
        resource: schoolFees,
        request: {
          method: "POST",
          url: "Basic"
        }
      })
      let bundles = [bundle]
      if(this.members.length) {
        for(let member of this.members) {
          if(!member.reg_resource) {
            continue
          }
          let bundle = {
            memberid: member.id,
            entry: [{
              resource: member.reg_resource,
              request: {
                method: "POST",
                url: "PractitionerRole"
              }
            }, {
              resource: member.family_head_resource,
              request: {
                method: "POST",
                url: "Basic"
              }
            }, {
              resource: member.fees,
              request: {
                method: "POST",
                url: "Basic"
              }
            }]
          }
          bundles.push(bundle)
        }
      }
      fetch(`/site/student/renewRegistration?practitioner=${this.queries.practitioner}`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        redirect: "manual",
        body: JSON.stringify(bundles),
      }).then((response) => {
        this.overlay = false;
        if(response.ok) {
          this.$store.commit("setMessage", {
            type: "success",
            text: this.$t("App.hardcoded-texts.Registration Renewed Successfully And Waiting Approval From Registration Office"),
          });
          this.$router.push("/custom/home?path=/itsf")
        } else {
          response.json().then((response) => {
            let text = "Failed to renew registration."
            if(response.message) {
              text = response.message
            }
            this.$store.commit("setMessage", {
              type: "error",
              text: text
            });
          }).catch(() => {
            this.$store.commit("setMessage", {
              type: "error",
              text: this.$t("App.hardcoded-texts.Failed to renew registration")
            });
          })
        }
      }).catch(() => {
        this.$store.commit("setMessage", {
          type: "error",
          text: "An error has occured, try later",
        });
      })
    },
    getName() {
      this.$fhirutils.resourceLookup("Practitioner/" + this.queries.practitioner).then((response) => {
        this.fullname = response
      })
    },
    populateValues(children) {
      for (let child of children) {
        if(child.qField && child.path) {
          this[child.path] = child.value
        }
        if (child.$children) {
          this.populateValues(child.$children)
        }
      }
    }
  },
  created() {
    this.getName()
    if(this.isMobile) {
      this.$store.state.minidrawer = true
    }
    this.overlay = true
    fetch("/site/student/registrationStatus/" + this.queries.practitioner).then((response) => {
      response.json().then((response) => {
        this.regStatus = response.status
        this.regStatusCode = response.statusCode
        if(this.regStatusCode === 'new') {
          this.edit = true
        }
      })
    })
    this.getLatestReg()
    this.getActiveFamilyMembers()
  }
}
</script>